* {
    box-sizing: border-box;
    margin: 0;
}

html {
    scroll-behavior: smooth;
    text-rendering: optimizeLegibility;
}

body {
    overflow-x: hidden;
    font-family: open-sans, 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: $clr-txt;
    background-color: $clr-body;

    button {
        cursor: pointer;
        border: none;
        background-color: transparent;
        padding: 0;
        outline: none;
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    strong {
        font-family: nunito, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
            'Open Sans', 'Helvetica Neue', sans-serif;
    }

    input,
    textarea {
        caret-color: $clr-logo;
    }

    input,
    textarea,
    button,
    select,
    a {
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    }

    a {
        color: inherit;
        text-decoration: none;
    }

    textarea {
        resize: none;
    }

    &.offline {
        .avatar,
        .work-preview {
            filter: grayscale(1);
        }
        .offline-indicator {
            position: fixed;
            bottom: 0;
            left: 0;
            right: 0;
            font-size: 0.9em;
            font-weight: bold;
            color: $clr-txt-darkmode;
            background-color: red;
            text-align: center;
            padding: 0.2rem;
            z-index: 1;
        }
    }

    &.dark-mode {
        color: $clr-txt-darkmode;
        background-color: $clr-body-darkmode;
        &::-webkit-scrollbar-thumb {
            background-color: $clr-footer-darkmode;
            border-color: $clr-footer-darkmode;
        }
    }

    &::-webkit-scrollbar {
        width: 5px;
    }

    &::-webkit-scrollbar-track {
        background: transparent;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $clr-footer;
        border-radius: 1px;
        border: 3px solid $clr-footer;
    }

    @media (max-width: $mobile-breakpoint) {
        font-size: 18px;
    }
}
